<template> 
  <div class="Login">
   <div style="height: 120px; overflow: hidden;">
    <img src="../../assets/img/login_header_bg.png" width="100%">
  </div> 
  <div class="login_tab">
    <span @click="loginPop(0)" v-bind:class="{active:tabIndex===0}" >登录</span>
    <span @click="loginPop(1)" v-bind:class="{active:tabIndex===1}">注册</span>
  </div>

  <div v-if="tabIndex === 0" class="whole" id="login_main"> 
    <div class="login-form"> 
      <div class="user-name common-div">
        <span class="phone-icon common-icon"></span>
        <input v-model="datalist.phone" type="text" value="" placeholder="请输入手机号" />        
      </div>
      <div class="user-pasw common-div">
        <span class="pasw-icon common-icon"></span>
        <input v-model="datalist.pass" type="password"  value="" placeholder="密码" />        
      </div>
      <div class="login-btn common-div" @click="loginForm(0)">登录</div> 
    </div>
    <div class="forgets">
      <a href="javascript:;" @click="loginPop(1)"><span class="color1">还没有账号?</span>去注册</a>
      <a href="#">忘记密码</a>
    </div>
  </div>


  <div v-if="tabIndex === 1" class="whole" id="reg_main"> 
    <div class="login-form"> 
      <div class="user-name common-div">
        <span class="phone-icon common-icon"></span>
        <input type="text" v-model="datalist_reg.phone" value="" placeholder="请输入手机号" />        
      </div>
      <div class="user-pasw common-div">
        <span class="pasw-icon common-icon"></span>
        <input type="password" v-model="datalist_reg.pass"  value="" placeholder="请设置密码" />        
      </div>
      <div class="user-code common-div">
        <span class="code-icon common-icon"></span>
        <input type="number" v-model="datalist_reg.chkCode" value="" placeholder="请输入验证码" />        
        <span class="send_btn" @click="sendCode(60)">发送验证码{{timerNum}}</span>
      </div>
      <div class="login-btn common-div" @click="loginForm(1)">注册</div> 
    </div>
    <div class="forgets">
      <a href="javascript:;" @click="loginPop(0)"><span class="color1">已有账号?</span>去登录</a>
      <a href="#">忘记密码</a>
    </div>
  </div>


</div>
</template>

<script>

import Axios from 'axios'
import { Toast } from 'mint-ui'
import Util from '@/assets/js/util.js'
const stat=0;
export default {
  name: 'Login',
  data () {
    return {
      tabIndex: 0,
      timerNum:'',
      datalist:{
        phone:null,
        pass:null,
        chkCode:null
      },
      datalist_reg:{
        phone:null,
        pass:null,
        chkCode:null
      },
      info:null

    }
  },
  mounted(){ 
    let user_id = sessionStorage.userId;
    if(!!user_id){
      window.location.href = '/ZiXun';
    }
  },    
  methods:{
    loginPop:function(index){ 
      this.tabIndex=index;
    },
    checkTelf:function(phone){ 
      //手机号正则
      var phoneReg = /(^1[3|4|5|6|7|8|9]\d{9}$)|(^09\d{8}$)/; 
      if (!phoneReg.test(phone)) {
        console.log('请输入有效的手机号码！');
        return false;
      }
    },
    checkPass:function(pass){
        var passReg =  /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/;
        if (!passReg.test(pass)) {
        console.log('密码长度至少8位最高20位，包含数字及大写小写字母！');
        return false;
      }

    },
    sendCode:function(count){
      var this_ = this;   
      var timestamp = new Date().getTime();

      var tpNew = sessionStorage.getItem("tp");

      console.log(tpNew);
      if( tpNew > timestamp){
        console.log("存储时间大于当前时间"); 
        return; //如果存储的时间 大于了当前时间   
      }else{
        Axios.get('/api/flxoa/registerCode?username='+this.datalist_reg.phone).then(function(response){ console.dir(JSON.stringify(response))
          if(response.status == '200'){ 
            console.log('验证码已发送成功');
            Toast("验证码已发送成功");
          } 
        });  
        daojishi();
      }

      var New_time = timestamp+61000;
      sessionStorage.setItem("tp",''+New_time);    
      console.log("------:"+sessionStorage.getItem("tp"));

      function daojishi(){
        const timer = setInterval(function () {
          if (count > 0) { 
            count = count - 1;
            this_.timerNum ='('+ count+')'; 
            console.log(count)
          }
          else {
            this_.timerNum = '';
            clearInterval(timer);   
          }
        }, 1000);
      } 
    }, 
    loginForm:function(t){
      const thisBase = this;
       var userName = Util.encrypt(this.datalist.phone,'abcdefgabcdefg12');
       var passWord = Util.encrypt(this.datalist.pass,'abcdefgabcdefg12'); 
      if(t==0){   
        if(this.datalist.phone==null){
   
          Toast("请输入手机号"); return;
        }   
        if(this.datalist.pass==null){Toast("请输入密码"); return;}    
         // console.log("源:"+userName+"(&&)"+passWord);
         console.log(userName);
         console.log(passWord);
         // console.log(":"+Util.decrypt(userName+"11111111"+passWord))
         if(thisBase.checkTelf(thisBase.datalist.phone) == false){
              Toast('请输入有效的手机号码！');
              return;
          }  

           if(thisBase.datalist.pass.length <= 8){
              Toast('密码至少8位！');
              return;
          }  
         var params = new URLSearchParams();
               params.append('username', userName);
               params.append('password', passWord);   
               params.append('dataType', 'json');   
        // this.axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
           //?username='+userName+'&password='+passWord
        // this.axios.post('/api/flxoa/LoginTelp',params).then(function(response){ 
        this.axios.get('/api/flxoa/LoginTelp?username='+userName+'&password='+passWord+'&dataType=json').then(function(response){ 
          console.dir(JSON.stringify(response))
          if(response.data.code == 200 && response.data.Msg == '成功'){ 
                    console.log('登录成功');Toast("登录成功");
                    sessionStorage.userId = thisBase.datalist.phone; 
                    console.log(sessionStorage.userId);  
                    window.location.href = '/ZiXun' ; 
                    // thisBase.$router.push({
                    //     path: '/zixun',
                    //     query: {
                    //        uid: thisBase.datalist.phone
                    //     }
                    // })
                  }else{
                    console.dir(response);Toast("登录失败");
                  }
                });  

      }
      if(t==1){
        if(this.datalist_reg.phone==null){Toast("请输入手机号"); return;}
        if(this.datalist_reg.pass==null){Toast("请输入密码"); return;}
        if(this.datalist_reg.chkCode==null){Toast("请输入验证码"); return;}
        if(thisBase.checkTelf(thisBase.datalist_reg.phone) == false){
              Toast('请输入有效的手机号码！');
              return;
          }  
         if(thisBase.checkPass(thisBase.datalist_reg.pass) == false){
              Toast('密码长度至少8位最高20位，包含数字及大写小写字母');
              return;
          } 

            this.axios.get('/api/flxoa/Register?username='+this.datalist_reg.phone+'&password='+this.datalist_reg.pass+'&code='+this.datalist_reg.chkCode).then(function(response){ 
              console.log('--------------------注册---------------------')
              console.log(response);
              if(response.data.code == 200 && response.data.Msg == '成功'){
              console.log('注册成功');Toast("注册成功");
              }
              thisBase.tabIndex = 0; 
            });  
      }
    }

  }

}


</script> 

<style scoped>
.aui-footer { display:none;} 
.common-div .send_btn{ width:130px;}
</style>



